/*------------------------------------*\
    #ICON
\*------------------------------------*/
/**
 * 1) Small image that represents functionality
 */
.c-icon {
  height: 1rem;
  width: 1rem;
}

/*------------------------------------*\
 #CARD
\*------------------------------------*/
/**
 * A collection of text and/or images and/or other smaller content 
 */
.c-card {
  display: block;
  border: 1px solid #bbb;
  padding: 1rem;
  max-width: 27rem;
  transition: all 0.15s ease-out;
}

/*------------------------------------*\
    #BUTTONS
\*------------------------------------*/
/**
 *
 * 1) Button or link that has functionality to it
 */
.c-btn {
  display: inline-block;
  border: 1px solid #1D72AF;
  background: #1D72AF;
  color: #FFF;
  line-height: 1;
  font-weight: 700;
  padding: 1rem 2rem;
  font-size: .875rem;
  text-transform: uppercase;
  border: 0;
  text-align: center;
  min-width: 195px;
  cursor: pointer;
  box-shadow: 0 0 0 0 rgba(1, 52, 128, 0.15);
  transition: all 0.15s ease-out;
}

.c-btn:hover,
.c-btn:focus {
  background: #155583;
  border-color: #155583;
  box-shadow: 0 5px 15px 0 rgba(1, 52, 128, 0.15);
}

.c-btn:visited {
  color: #FFF;
}

.c-btn--action {
  border-color: #FFBA00;
  background: #FFBA00;
  color: #000;
}

.c-btn--action:hover,
.c-btn--action:focus {
  background: #FFCB40;
  border-color: #FFCB40;
}

.c-btn--action:visited {
  color: #000;
}

.c-btn-video {
  display: flex;
  align-items: center;
  appearance: none;
  font-weight: 700;
  color: #1D72AF;
  border: none;
  background: none;
  padding: 0;
  transition: .3s;
}

.c-btn-video:disabled,
.c-btn-video[disabled] {
  cursor: not-allowed;
  pointer-events: none;
  opacity: .6;
}

.c-btn-video>.c-icon {
  margin-right: .5rem;
  transition: .3s;
}

.c-btn-video:hover,
.c-btn-video:focus {
  color: #155583;
}

.c-btn-video:hover>.c-icon {
  background: #155583;
  border-color: #155583;
}

.c-btn-video:hover>.c-icon--play:before {
  border-color: transparent transparent transparent #FFFFFF;
}

.c-btn-to-top-wrap {
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 82rem;
  text-align: right;
  z-index: 100;
}

.c-btn-to-top {
  display: inline-block;
  padding: .5rem 1rem;
  border: 1px solid #1D72AF;
  background: #1D72AF;
  color: #FFFFFF;
  border-radius: 5px;
  font-size: .75rem;
  font-weight: 500;
  box-shadow: 0 10px 30px 0 rgba(1, 52, 128, 0.15);
  transition: .3s;
}

.c-btn-to-top:hover,
.c-btn-to-top:focus {
  background: #155583;
  border-color: #155583;
}

/*
 * Inner container wrapper
 * 1) Container is direct decendant of <button>, since there's
 *    a bug with applying flexbox directly to a <button>
 * 2) Only needs to be used for buttons containing both icons and text
 */
.c-btn__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*
 * Button icon
 */
.c-btn__icon {
  width: 1rem;
  height: 1rem;
  fill: #fff;
  transition: fill 0.15s ease-out;
}

.c-arrow-link {
  display: flex;
  align-items: center;
  color: #1D72AF;
  font-weight: 700;

  &--prev {
    &::after {
      display: none !important;
    }

    &::before {
      content: "";
      display: inline-block;
      background: url(../images/icon_arrow-right-blue.svg);
      margin-right: .5rem;
      height: 16px;
      width: 16px;
      background-size: 16px;
      transform: scaleX(-1);
      background-repeat: no-repeat;
      -webkit-transition: .3s;
      transition: .3s;
    }

    &:hover,
    &:focus {
      &::before {
        background: url(../images/icon_arrow-right-blue.svg);
        background-size: 16px;
        transform: translateX(-0.5rem) scaleX(-1);
      }
    }
  }
}

.c-arrow-link:after {
  content: "";
  display: inline-block;
  background: url('../images/icon_arrow-right-blue.svg');
  margin-left: .5rem;
  height: 16px;
  width: 16px;
  background-size: 16px;
  background-repeat: no-repeat;
  transition: .3s;
}

.c-arrow-link:hover,
.c-arrow-link:focus {
  color: #155583;
}

.c-arrow-link:hover:after,
.c-arrow-link:focus:after {
  background: url('../images/icon_arrow-right-blue-hover.svg');
  background-size: 16px;
  transform: translateX(.5rem);
}

/*------------------------------------*\
    #FIELDS
\*------------------------------------*/
/**
 * 1) Consists of a label, form control, and an optional note about the field.
 */
.c-field {
  margin-bottom: 2rem;
}

/**
 * Field label
 */
.c-field__label {
  margin-bottom: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
}

/**
 * Field body
 */
.c-field__body {
  position: relative;
}

/**
 * Field note
 */
.c-field__note {
  display: inline-block;
  font-size: 0.75rem;
  color: #808080;
}

/*------------------------------------*\
    #LOGO
\*------------------------------------*/
/**
 * Branding image or text of the site
 */
.c-logo {
  max-width: 9rem;
}

@media (min-width:991px) {
  .c-logo {
    max-width: 14rem;
  }
}

/**
 * Logo link
 */
.c-logo__link {
  display: block;
}

/**
 * Logo image
 */
.c-logo__img {
  display: block;
}

/*------------------------------------*\
 #GRID
\*------------------------------------*/
/**
 * Primary Grid  
 */
.l-grid {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  margin-bottom: 2rem;
}

/*------------------------------------*\
    #LAYOUT CONTAINER
\*------------------------------------*/
/**
 * Layout Container
 * 1) Caps the width of the content to the maximum width
 *    and centers the container
 */
.l-container {
  max-width: 82rem;
  margin: 0 auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 84rem) {
  .l-container {
    padding-left: 0;
    padding-right: 0;
  }
}

.l-grad-container .c-comp:first-child {
