}

/*------------------------------------*\
    #PRIMARY NAVIGATION
\*------------------------------------*/
/**
 * Primary navigation existing in the header and maybe the footer
 */
.c-primary-nav {
  margin-left: auto;
}

/**
 * Primary navigation list
 */
.c-primary-nav__list {
  display: flex;
}

/**
 * Primary navigation item
 */
.c-primary-nav__item {
  position: relative;
  margin-left: 2rem;
  font-weight: 500;
}

.c-primary-nav__item:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  opacity: 0;
  height: 0;
  background: #FFBA00;
  transition: .2s;
}

.c-primary-nav__item:hover:after {
  height: 4px;
  opacity: 1;
}

/**
 * Primary navigation link
 */
.c-primary-nav__link {
  display: block;
  padding: 2rem 0 2rem;
  cursor: pointer;
}

/**
 * Primary navigation dropdown
 */
.c-primary-nav__dropdown {
  z-index: 20;
  background: #FFFFFF;
}

@media (min-width: 990px) {
  .c-primary-nav__dropdown {
    position: absolute;
    top: calc(100% + 1px);
    left: 0;
    padding: 2rem 4rem 2rem 2.875rem;
    box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.07);
    min-width: 200px;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    column-count: 2;
    column-gap: 4rem;
    column-rule-width: 200px;
  }

  .c-primary-nav__item:hover .c-primary-nav__dropdown {
    opacity: 1;
    visibility: visible;
  }
}

/**
 * Primary navigation dropdown title
 */
.c-primary-nav__dropdown-title {
  column-span: all;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.c-primary-nav__dropdown-title>a {
  color: #1D72AF;
}

/**
 * Primary navigation dropdown item
 */
.c-primary-nav__dropdown-item {
  white-space: nowrap;
  font-weight: 400;
  font-size: .875rem;
  margin-bottom: 1rem;
}

@media (min-width: 990px) {
  .c-primary-nav__dropdown-item {
    min-width: 200px;
  }
}

/*------------------------------------*\
    #PAGE HEADER
\*------------------------------------*/
/**
 * 1) Container that consists of of a page header title and description
 */
/**
 * Page header title
 */
.c-page-header__title {
  margin-bottom: 0;
}

/**
 * Page description
 */
.c-page-header__description {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

/*------------------------------------*\
 #FORM MOLECULES
\*------------------------------------*/
.c-form-select {
  appearance: none;
  border: none;
  background-size: 1rem;
  background: url(../../images/select-arrow.svg) right center no-repeat;
}

/*------------------------------------*\
    FOOTER
\*------------------------------------*/
/**
 * 1) Global block at the bottom of each page that contains a navigation and other information
 * 2) Footer layout.
 */
.c-footer {
  padding: 2rem 0;
  border-top: 2px solid #FFBA00;
}

.c-footer__top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #D7D9DA;
  margin-bottom: 2rem;
}

.c-footer__top>img {
  margin-bottom: 2rem;
}

.c-footer__mid {
  display: flex;
  flex-direction: column;
  font-size: .875rem;
}

.c-footer__bottom {
  display: flex;
  flex-direction: column;
  font-size: .75rem;
}

.c-footer__col {
  margin-bottom: 3rem;
}

@media (min-width:768px) {
  .c-footer__top {
    flex-direction: row;
    justify-content: space-between;
  }

  .c-footer__top>img {
    margin-bottom: 1rem;
  }

  .c-footer__bottom {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width:990px) {
  .c-footer__mid {
    flex-direction: row;
  }

  .c-footer__col {
    padding: 0 1rem;
    flex: 1 1 auto;
  }

  .c-footer__col--lg {
    flex: 2 1 auto;
    max-width: 30%;
  }
}

/**
 * 1) Styling of footer UI elements
 */
.c-footer a:hover,
.c-footer a:focus {
  text-decoration: underline;
}

.c-footer-title {
  display: block;
  margin-bottom: .5rem;
}

.c-footer-title--accented {
  margin-bottom: 0;
}

.c-footer-title--accented:after {
  content: "";
  display: block;
  width: 20px;
  height: 3px;
  margin: .25rem 0 .75rem;
  background: #FFDA76;
}

.c-footer-address {
  font-style: normal;
  margin-bottom: 1.5rem;
}

.c-footer-nav {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.c-footer-nav--col {
  display: block;
  margin-bottom: 0;
}

@media (min-width:768px) {
  .c-footer-nav {
    margin-bottom: 0;
  }

  .c-footer-nav--dual {
    column-count: 2;
  }
}

.c-footer-nav__item {
  margin-right: 2rem;
}

.c-footer-nav--col .c-footer-nav__item {
  margin-bottom: .5rem;
  margin-right: 0;
}

.c-footer-nav--col .c-footer-nav__item:last-child {
  margin-bottom: 0;
}

.c-footer__contact>li {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1rem;
}

.c-footer__contact>li>.c-icon {
  margin-right: 1rem;
  width: 40px;
  height: 40px;
  line-height: 40px;
}

.c-footer__contact>li>.c-icon>img {
  max-width: 14px;
}

.c-footer__contact>li span {
  display: block;
  font-weight: 300;
}

.c-footer__contact>li a {
  color: #1D72AF;
  font-weight: 700;
}

/*------------------------------------*\
    #HEADER
\*------------------------------------*/
/**
 * Global block at the top of each page containing the navigation, logo, and other potential contents
 */
.c-header {
  position: relative;
  background: #FFFFFF;
  border-bottom: 1px solid #FFBA00;
  box-shadow: 0 5px 15px 0 rgba(1, 52, 128, 0.15);
  padding: 1rem 1rem 0;
}

/**
 * Header navigation conntainer
 * 1) Contains the primary navigation and other possible patterns
 */
.c-header__nav {
  position: fixed;
  opacity: 0;
  visibility: hidden;
  left: -100%;
  top: 100px;
  height: 100%;
  background: #FFFFFF;
}

.c-header__nav.is-open {
  opacity: 1;
  visibility: visible;
  left: 0;
  z-index: 10;
}

@media (min-width:990px) {
  .c-header__nav {
    position: inherit;
    opacity: 1;
    visibility: visible;
    left: auto;
  }
}

/**
 * Header inner
 */
.c-header__inner {
  display: flex;
  width: 100%;
  max-width: none;
  justify-content: space-between;
  align-items: center;
}

@media (min-width:990px) {
  .c-header__inner {
    align-items: flex-end;
  }
}

/**
 * Header triggers
 */
.c-header__btn {
  display: inline-block;
}

@media (min-width:990px) {
  .c-header__btn {
    display: none;
  }
}

/**
 * Header subnav container
 */
.c-header-subnav {
  position: inherit;
  top: 0;
  right: 0;
  width: 75%;
  padding: .75rem 1rem;
  font-size: .875rem;
}

.c-header-subnav__list {
  display: block;
  border-bottom: 1px solid;
}

.c-header-subnav__list:last-of-type {
  border: none;
}

.c-header-subnav__list>li {
  margin: .5rem 0;
}

.c-header-subnav__link {
  color: #000000;
}

.c-header-subnav__link:hover,
.c-header-subnav__link:focus {
  text-decoration: underline;
}

.c-header-subnav__search {
  display: none;
}

@media (min-width:990px) {
  .c-header-subnav {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    background: linear-gradient(90deg, rgba(240, 247, 252, 0) 0%, #F0F7FC 100%);
  }

  .c-header-subnav__list {
    display: flex;
    align-items: center;
    border: none;
  }

  .c-header-subnav__list>li {
    margin: 0 .5rem;
  }

  .c-header-subnav__list:after {
    content: "|";
    display: inline-block;
    color: #969491;
    margin: 0 .5rem;
  }

  .c-header-subnav__search {
    display: inline-block;
    font-size: .875rem;
    margin-left: .5rem;
  }
}

/**
 * Active header nav container
 */
.c-header__nav-container.is-active {
  display: block;
  position: absolute;
  background: #eee;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 1rem;
  /**
	 * Active header nav container within vertical header
	 */
}

@media all and (min-width: 70em) {
  .c-header__nav-container.is-active {
    display: block;
    position: static;
    padding: 0;
    margin-left: auto;
    width: inherit;
  }
}

@media all and (min-width: 70em) {
  .c-header--vertical .c-header__nav-container.is-active {
    margin-left: 0;
    width: 100%;
  }
}

