  height: 0;
  margin-left: 2px;
  border-style: solid;
  border-width: 8px 0 8px 13.9px;
  border-color: transparent transparent transparent currentColor;
}

.c-icon>img,
.c-icon>svg {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 25px;

  &.checkmark {
    width: 15px;
    height: 11px;
  }
}

